import React from "react";

import "./GraphicDesigningMainDescription.css";

import { useNavigate } from "react-router-dom";

export default function GraphicDesigningMainDescription(prop) {
  const navigate = useNavigate();

  return (
    <div className="graphic-designing-main-description">
      <h1>{prop.heading}</h1>

      <p>{prop.paragraph}</p>

      {prop.showButton && (
        <button
          onClick={() => {
            navigate("/contactus");
          }}
        >
          Let's Chat
        </button>
      )}
    </div>
  );
}
